import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class DiscountsService {
  constructor(private http: HttpClient) {}

  discounts = new BehaviorSubject(null);

  getAll() {
    return this.http.get(baseUrl + '/discount').subscribe(
      (res) => {
        if (res['data']) {
          this.discounts.next(res['data']);
        }
      },
      (error) => {
        console.log(error);
        // this.error.next(error.error.message);
      }
    );
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/discount/${itemId}`);
  }

  addAdvertise(itemId, form) {
    return this.http.post(baseUrl + `/discount/advertise/${itemId}`, form);
  }
}
